<template>
  <right-page :header="rightPageHeader" :hide-content-padding="true">
    <div slot="content">
      <section class="section">
        <div class="slds-form-element form-section">
          <label class="slds-form-element__label" for="ClientId">
            <abbr class="slds-required" title="required">* </abbr>Client ID
          </label>
          <div class="slds-form-element__control">
            <input id="ClientId" class="slds-input" type="text" v-model="secret.ClientId"/>
          </div>
        </div>
        <div class="slds-form-element form-section">
          <label class="slds-form-element__label" for="ClientSecret">
            <abbr class="slds-required" title="required">* </abbr>Client Secret
          </label>
          <div class="slds-form-element__control">
            <input id="ClientSecret" class="slds-input" type="text" v-model="secret.ClientSecret"/>
          </div>
        </div>
        <div class="slds-form-element form-section">
          <label class="slds-form-element__label" for="AuthenticationBaseUri">
            <abbr class="slds-required" title="required">* </abbr>Authentication Base URI
          </label>
          <div class="slds-form-element__control">
            <input id="AuthenticationBaseUri" class="slds-input" type="text" v-model="secret.AuthenticationBaseUri"/>
          </div>
        </div>
        <div class="slds-form-element form-section">
          <label class="slds-form-element__label" for="JWTSigningSecret">
            <abbr class="slds-required" title="required">* </abbr>JWT Signing Secret
          </label>
          <div class="slds-form-element__control">
            <input id="JWTSigningSecret" class="slds-input" type="text" v-model="secret.JWTSigningSecret"/>
          </div>
        </div>
        <div class="slds-form-element form-section">
          <label class="slds-form-element__label" for="RESTBaseURI">
            <abbr class="slds-required" title="required">* </abbr>REST Base URI
          </label>
          <div class="slds-form-element__control">
            <input id="RESTBaseURI" class="slds-input" type="text" v-model="secret.RestBaseUri"/>
          </div>
        </div>
      </section>
      <footer class="slds-modal__footer">
        <button class="slds-button slds-button_neutral" :disabled="!hasChanged || isSaving" @click="cancel()">Cancel</button>
        <button class="slds-button slds-button_brand" :disabled="!hasChanged || isSaving || !canSave" @click="saveJWTSecret()">Save</button>
      </footer>
    </div>
  </right-page>
</template>

<script>
import { getMCJWTSigningSecret, saveMCJWTSigningSecret } from '../../src/api/charket-settings.js'

export default {
  name: 'JWTSettings',
  data () {
    return {
      rightPageHeader: {
        label: 'CHARKET SETTINGS',
        name: 'Marketing Cloud Auth. Settings'
      },
      secret: {},
      secretCache: {},
      isSaving: false
    }
  },
  computed: {
    hasChanged () {
      return this.secretCache.JWTSigningSecret !== this.secret.JWTSigningSecret ||
      this.secretCache.AuthenticationBaseUri !== this.secret.AuthenticationBaseUri ||
      this.secretCache.ClientSecret !== this.secret.ClientSecret ||
      this.secretCache.ClientId !== this.secret.ClientId ||
      this.secretCache.RestBaseUri !== this.secret.RestBaseUri
    },
    canSave () {
      return this.secret.JWTSigningSecret && this.secret.AuthenticationBaseUri &&
      this.secret.ClientSecret && this.secret.ClientId && this.secret.RestBaseUri
    }
  },
  created () {
    this.fetchJWTSecret()
  },
  methods: {
    async fetchJWTSecret () {
      try {
        this.$bus.emit('show-loading')
        this.secret = await getMCJWTSigningSecret()
        this.secretCache = JSON.parse(JSON.stringify(this.secret))
      } catch (error) {
        this.$bus.emit('error', { content: error.msg, type: 'error' })
      } finally {
        this.$bus.emit('hide-loading')
      }
    },
    cancel () {
      this.secret = JSON.parse(JSON.stringify(this.secretCache))
    },
    async saveJWTSecret () {
      try {
        this.isSaving = true
        this.secret = await saveMCJWTSigningSecret(JSON.stringify(this.secret))
        this.secretCache = JSON.parse(JSON.stringify(this.secret))
        this.$bus.emit('success', { content: 'Marketing Cloud Auth. Settings was saved.', type: 'success' })
      } catch (error) {
        this.$bus.emit('error', { content: error.msg, type: 'error' })
      } finally {
        this.isSaving = false
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .section {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 20px 28px 30px 20px;
    .form-section {
      width: 48%;
    }
  }
</style>
